/* Plans.css */

/* Main container styles */
.main-card-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 20px;
  justify-items: center;
  padding: 20px;
}

/* Card styles */
.card-container {
  width: 100%;
  max-width: 300px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  transition: -webkit-transform 0.2s;
  transition: transform 0.2s;
  transition: transform 0.2s, -webkit-transform 0.2s;
}

.card-container:hover {
  -webkit-transform: translateY(-5px);
          transform: translateY(-5px);
}

.plan {
  padding: 16px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.plan-image {
  width: 100%;
  height: 200px;
  overflow: hidden;
  border-radius: 8px 8px 0 0;
}

.image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.plan-body {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.title {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 8px;
}

.description {
  font-size: 16px;
  color: #555;
  margin-bottom: 8px;
}

.price {
  font-size: 18px;
  font-weight: bold;
  color: #009688;
  margin-bottom: 8px;
}

.adminID{
  font-size: 18px;
  font-weight: bold;
  color: #009688;
  margin-bottom: 8px;
}

button.btn_view_more {
  background-color: #009688;
  color: #fff;
  border: none;
  border-radius: 4px;
  padding: 8px 16px;
  cursor: pointer;
  font-size: 16px;
}

/* Responsive styles */
@media screen and (max-width: 768px) {
  .main-card-container {
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  }
}

/* TravelPlanPage.css */

/* Container styles */
.cover {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  background-color: #f0f0f0;
  
}

/* Plan details styles */
.div_1 {
  margin-bottom: 20px;
  padding: 20px;
  border: 2px solid #ccc;
  border-radius: 8px;
  background-color: #fff;
  max-width: 600px;
  text-align: center;

}

.name{
  font-size : 35px;
  text-decoration:underline;
  margin-bottom: 3%;
}

.description{
  font-size : 15px;
  color :#9a0630;
}

.admin{
  font-size : 15px;
  color :#9a0630;
  margin-bottom: 12px;
}

.start_date{
  font-size : 20px;
  color :#9a0630;
  margin-bottom: 12px;
  margin-left: 4%;
}

.div_1 img {
  max-width: 100%;
  max-height: 200px;
  object-fit: cover;
  margin-bottom: 10px;
}

/* Button styles */
.div_2,
.div_3,
.div_4 {
  margin: 10px 0;
  display: flex;
  grid-gap: 10px;
  gap: 10px;
}

.btn_1,
.btn_2,
.btn_3,
.btn_4,
.btn_5 {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  color: #fff;
  font-weight: bold;
}

.btn_1 {
  background-color: #ff4500;
}

.btn_2 {
  background-color: #ffa500;
}

.btn_3 {
  background-color: #4caf50;
}

.btn_4 {
  background-color: #ff4500;
}

.btn_5 {
  background-color: #4caf50;
}

/* Registered users styles */
.div_5 {
  margin: 20px;
  padding: 20px;
  border: 2px solid #4caf50;
  border-radius: 8px;
  background-color: #f0f8ff;
}

.registered {
  font-weight: bold;
  margin-bottom: 10px;
}

.users ul {
  list-style: none;
  padding: 0;
}

.users li {
  margin: 5px;
}

.not_registered {
  font-weight: bold;
  color: #ff0000;
}

/* Error message styles */
.users {
  margin: 10px;
  padding: 10px;
  border: 2px solid #ff0000;
  border-radius: 5px;
  background-color: #ffebee;
  color: #ff0000;
  font-weight: bold;
}

/* Responsive styles */
@media screen and (max-width: 768px) {
  .div_1 {
    max-width: 100%;
  }
}

@media screen and (max-width: 576px) {
  .div_1 {
    padding: 10px;
  }

  .div_2,
  .div_3,
  .div_4 {
    flex-direction: column;
  }

  .btn_1,
  .btn_2,
  .btn_3,
  .btn_4,
  .btn_5 {
    width: 100%;
  }
}

